<template>
  <div class="houseSearch">
    <navigation-bar  @onLeftClick="goBack" :navBarStyle="navStyle" leftImgType="1">
      <div class="nav-box" slot="nav-center">
        <div class="search">
          <van-icon name="search" size="14px" color="#aaa"/>
          <input type="text" placeholder="请输入区域/小区名称" v-model="areaName" ref="myInput">
        </div>
        <div class="search-btn" @click="search">
            搜索
        </div>
      </div>
    </navigation-bar>
  </div>
</template>

<script>
import leftIcon from "@/assets/images/common/nav_return_white.png";
import NavigationBar from "@/common/currency/NavigationBar.vue";
export default {
  name: "houseSearch",
  data() {
    return {
      areaName: "",
      isSearch: false,
      leftIcon: leftIcon,
      navStyle: {
        background: "rgb(251, 87, 104)"
      }
    };
  },
  mounted() {
    this.$nextTick(function() {
      this.$refs.myInput.focus();
    });
  },
  components: {
    NavigationBar
  },
  created() {},
  methods: {
    /**
     * 返回事件
     */
    goBack() {
      this.$router.go(-1);
    },

    /**
     * 搜索事件
     */
    search() {
      this.$store.dispatch("setAreaName", this.areaName);
      this.$store.dispatch("setReload", true);
      this.$router.go(-1);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.houseSearch {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  .nav-box {
    width: 242px;
    margin: 0 auto;
    height: 44px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .search {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      width: 174px;
      padding: 0 8px;
      box-sizing: border-box;
      height: 28px;
      border-radius: 14px;
      background: #ffffff;
      input {
        width: 130px;
        height: 20px;
        border: none;
        margin: 0;
        padding: 0;
        font-size: 14px;
        margin-left: 8px;
        outline: none;
        background: #ffffff;
        color: #000000;
        line-height: 20px;
      }
      input::-webkit-input-placeholder {
        color: #c9c9c9;
        font-size: 12px;
      }
    }
    .search-btn {
      width: 52px;
      text-align: center;
      margin-left: 8px;
      height: 44px;
      line-height: 44px;
      font-size: 13px;
      color: #fff;
    }
  }
  /deep/.van-nav-bar {
    background: #fb5768;
  }
  /deep/.van-nav-bar__title {
    color: #ffffff;
  }
  /deep/.van-nav-bar__title {
    overflow: visible;
  }
}
</style>
